<template>
    <div>
        <div
                class="modal fade"
                id="edit-my-poll-popup"
                tabindex="-1"
                role="dialog"
                aria-labelledby="edit-my-poll-popup"
                aria-hidden="true"
        >
            <div class="modal-dialog window-popup edit-my-poll-popup" style="width: 600px;" role="document">
                <div class="modal-content" style="background-color: transparent;border: none">
                    <div class="modal-body" ref="showMatchReviewBox">
                        <div class="edit-my-poll-content no-padding">
                            <response-message :data="errorData" />
                            <form method="post" enctype="multipart/form-data" @submit.prevent="reviewListerner" class="resume-form">
                                <div class="ttweetBox">
                                    <div class="ttweetBoxHeader">
                                        <a href="#" class="close icon-close" data-dismiss="modal" aria-label="Close">
                                            <svg class="olymp-close-icon">
                                                <use xlink:href="svg-icons/sprites/icons.svg#olymp-close-icon" />
                                            </svg>
                                        </a>
                                        <p style="float: left">{{ reviewType === 'review' ? 'Write Review' : 'Update review' }} </p>
                                        <div v-if="matchId !== '0' && home_team_logo !== '' " style="float: left;padding-left: 10px">
                                            <span style="color: #0c5460;font-weight: bold;float: left"> for</span>
                                            <div class="small-vs-logo">
                                                <img :src="home_team_logo">
                                                <p>{{ homeTeamName }}</p>
                                            </div>
                                            <span style="color: #000;font-weight: bold;float: left;position: relative;top: 5px;margin: 0 10px;">VS</span>
                                            <div class="small-vs-logo">
                                                <img :src="away_team_logo">
                                                <p>{{ awayTeamName }}</p>
                                            </div>
                                        </div>
                                    </div>
                                    <div class="ttweetBoxBody">
                                        <div class="ttweetBoxImage ">
                                            <div class="img">
                                                <img :src="getAvatarLocal()">
                                            </div>
                                        </div>
                                        <div class="ttweetBoxText">
                                            <textarea
                                                    v-model="formModelReview.review"
                                                    name="review"
                                                    v-on:keyup.ctrl.enter="reviewListerner"
                                                    placeholder="What's on your mind"
                                                    id="review"
                                            ></textarea>
                                            <div class="uploadedFile">
                                                <span style="width: 100%"></span>
<!--                                                <img src="/banner/EJA1dfFWsAAC1p7.jpg">-->
                                                <div v-if="fileType === 'video' " id="videoDiv">
                                                    <div id="videoBlock">
                                                        <video id="video" class="popUpModVid" preload="auto" src="" ></video>
                                                        <div @click="setVideo()" class="pausePlayBox popUpMod">
                                                            <i class="pplay fa fa-play-circle"></i>
                                                        </div>
                                                    </div>
                                                </div>
                                                <div @click="removeSelectedPicture()" class="uploadedFileCancle">
                                                    <svg class="olymp-like-post-icon"><use xlink:href="/svg-icons/sprites/icons.svg#olymp-little-delete"></use></svg>
                                                </div>
<!--                                                <div @click="editSelectedPicture()" class="uploadedFileEdit">-->
<!--                                                    <i class="las la-pen-fancy"></i>-->
<!--                                                </div>-->
                                            </div>
                                        </div>
                                    </div>
                                    <div class="ttweetBoxFooter">
                                        <div class="box1">
                                            <input
                                                    type="file"
                                                    @change="previewImage"
                                                    style="display:none"
                                                    id="getImage"
                                                    name="image_name"
                                            />
                                            <div style="display: flex;justify-content: space-between;width: 120px" class="">
                                                <div>
                                                    <svg @click="getImage('image')" class="olymp-like-post-icon"><use xlink:href="/svg-icons/sprites/icons.svg#olymp-camera-icon"></use></svg>
                                                    <p class="text-center">Image</p>
                                                </div>
                                                <div>
                                                    <svg @click="getImage('video')" class="olymp-like-post-icon pplay"><use xlink:href="/svg-icons/sprites/icons.svg#olymp-play-icon"></use></svg>
                                                    <p class="text-center">Video</p>
                                                </div>
                                            </div>
                                        </div>
                                        <div class="box2">
                                            <button
                                                    id="reviewPostButton"
                                                    class="btn bg-mdd btn-md-2 full-width"
                                            > {{ reviewType === 'review' ? 'Post' : 'Update' }}</button>
                                        </div>
                                    </div>
                                </div>
                            </form>

                        </div>
                    </div>
                </div>
            </div>
        </div>
<!--        This is the section for modal quit option-->
        <div class="modal fade" id="quiteOption" tabindex="-1" role="dialog" aria-labelledby="exampleModalCenterTitle" aria-hidden="true">
            <div class="modal-dialog modal-dialog-centered" role="document">
                <div class="modal-content">
                    <div class="modal-header">
                        <h5 class="modal-title" id="exampleModalLongTitle">Modal title</h5>
                        <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                            <span aria-hidden="true">&times;</span>
                        </button>
                    </div>
                    <div class="modal-body">
                        ...
                    </div>
                    <div class="modal-footer">
                        <button type="button" class="btn btn-secondary" data-dismiss="modal">Close</button>
                        <button type="button" class="btn btn-primary">Save changes</button>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import {mapState} from "vuex";
    import avta from "@/functions/getAvatar";

    export default {
        name: "PostReview.vue",
        components: {},
        data() {
            return {
                homeTeamName: "",
                awayTeamName: "",
                home_team_logo: "",
                away_team_logo: "",
                matchId: "",
                errorData: {},
                imageData: "string",
                imageName: "",
                formModelReview: {
                    review: "",
                },
                reviewType: '',
                percentage: 10,
                fileType: '',
                selectedVideoFile: '',
                review_id: '',
                index: '',
                feedDetails: '',
                imgSrc: '',
            };
        },
        computed: mapState({
            getAvatar: state => state.user.avatar,
            getUsername: state=> state.user.username,
            getUserContributions: state => state.contributions,
        }),
        methods: {
            setVideo() {
                const vvid = $('.popUpModVid')[0]
                if (vvid.paused) {
                    vvid.play();
                    $('.popUpMod').hide()
                } else {
                    vvid.pause();
                }
            },
            getAvatarLocal() {
                // let userData = localStorage.fansData;
                let data = avta({state:{ av: this.getAvatar, us: this.getUsername }});
                return data;
            },
            setMatch() {
                // let loader = this.showLoading();
                this.errorData = {
                    isError: false,
                };

                if (this.matchId !== '0') {
                    const feed = this.feedDetails;
                    if (feed !== undefined) {
                        this.homeTeamName = feed.match.home_team_name;
                        this.awayTeamName = feed.match.away_team_name;
                        this.home_team_logo = feed.match.home_team_badge;
                        this.away_team_logo = feed.match.away_team_badge;
                    }else {
                        const matchId = this.matchId;
                        this.axios
                            .get(`matches/single_by_key/${matchId}`)
                            .then(response => {
                                // loader.hide();
                                // console.log(response.data.data.match);
                                const match = response.data.data.match;
                                this.matchId = matchId;
                                this.homeTeamName = match.home_team_name;
                                this.awayTeamName = match.away_team_name;
                                this.home_team_logo = match.home_team_badge;
                                this.away_team_logo = match.away_team_badge;
                            })
                            .catch(() => {
                                // loader.hide();
                                $("#edit-my-poll-popup").modal("hide");
                                // console.log(error.response);
                            });
                    }
                }

            },
            getImage(type) {
                this.fileType = type
                $("#getImage").click();
            },
            // get file size
            humanFileSize(size) {
                var i = Math.floor( Math.log(size) / Math.log(1024) );
                return ( size / Math.pow(1024, i) ).toFixed(2) * 1 + ' ' + ['B', 'kB', 'MB', 'GB', 'TB'][i];
            },
            editSelectedPicture() {
                let file = $("#getImage").get(0).files[0];
                if (typeof FileReader === 'function') {
                    const reader = new FileReader();

                    reader.onload = (event) => {
                        // console.log(event)
                        this.imgSrc = event.target.result;
                        // rebuild cropperjs with the updated source
                        $('.mod').removeClass('d-none')
                    };
                    reader.readAsDataURL(file);
                } else {
                    console.log('Sorry we just faced an error')
                }
            },
            previewImage(ev) {
                this.errorData = {};
                let file = $("#getImage").get(0).files[0];
                // console.log(file.size)
                if(file.size > 30529791 || file.size > 30529791 ) {
                    this.errorData = {
                        message: "Sorry only files less than 16 MB",
                        isError: true,
                    };
                } else {
                    if ($("#getImage").get(0).files.length > 0) {
                        $('#reviewPostButton').attr('disabled', false)
                        if (this.fileType === 'image') {
                            if (
                                $("#getImage")
                                    .get(0)
                                    .files[0].name.split(".")
                                    .pop() !== "png" &&
                                $("#getImage")
                                    .get(0)
                                    .files[0].name.split(".")
                                    .pop() !== "jpg" &&
                                $("#getImage")
                                    .get(0)
                                    .files[0].name.split(".")
                                    .pop() !== "jpeg" &&
                                $("#getImage")
                                    .get(0)
                                    .files[0].name.split(".")
                                    .pop() !== "gif"
                            ) {
                                this.errorData = {
                                    message: "Sorry images can only be (Jpg - Png - Gif)",
                                    isError: true,
                                };
                            } else {
                                const url = ev.target.files[0]
                                // here we check if the type of files fits in to our video list
                                // if so we know how to preview the video
                                // else we preview the image
                                // image here
                                const imagee = `<img src="${URL.createObjectURL(url)}">`
                                $('.uploadedFile').css('display', 'flex');
                                $('.uploadedFileCancle').css('display', 'flex');
                                $('.uploadedFileEdit').css('display', 'flex');
                                $('.uploadedFile span').html(imagee)

                            }
                        } else if (this.fileType === 'video') {
                            if (
                                $("#getImage")
                                    .get(0)
                                    .files[0].name.split(".")
                                    .pop() !== "mp4" &&
                                $("#getImage")
                                    .get(0)
                                    .files[0].name.split(".")
                                    .pop() !== "m4v" &&
                                $("#getImage")
                                    .get(0)
                                    .files[0].name.split(".")
                                    .pop() !== "MOV" &&
                                $("#getImage")
                                    .get(0)
                                    .files[0].name.split(".")
                                    .pop() !== "mkv"

                            ) {
                                this.errorData = {
                                    message: `Sorry videos can only be (Mp4, M4v, Mov, Mkv)`,
                                    isError: true,
                                };
                            } else {
                                let file = $("#getImage").get(0).files[0];
                                const type = file.name.split(".").pop()
                                const url = ev.target.files[0]
                                // console.log('file size ', this.humanFileSize(file.size))

                                // here we check if the type of files fits in to our video list
                                // if so we know how to preview the video
                                // else we preview the image
                                if (type === 'mp4' || type === 'm4v' || type === 'MOV' || type === 'mkv') {
                                    // video here
                                    $('.uploadedFile').css('display', 'block');
                                    $('.uploadedFileCancle').css('display', 'flex');
                                    this.selectedVideoFile = URL.createObjectURL(url)
                                    $('.popUpModVid').attr('src', URL.createObjectURL(url))
                                }
                            }
                        } else {
                            $('#reviewPostButton').attr('disabled', true)
                        }
                    }
                }
            },
            removeSelectedPicture() {
                $('#getImage').val('');
                $('.uploadedFile').css('display','none');
                $('.uploadedFileCancle').css('display','none');
                $('.uploadedFile span').html('')
                $('.uploadedFile video').attr('src','')
            },
            getImageDetails() {
                if ($("#getImage").get(0).files.length > 0) {
                    let file = $("#getImage").get(0).files[0].name;
                    $("#imageName").val(file);
                    var splitImageName = file.split(".");
                    return splitImageName[0];
                }
            },
            clearForm() {
                this.formModelReview.review = "";
                this.matchId = "",
                    $("#getImage").val("");
                $("#imageName").val("");
            },
            reviewListerner() {
                // get the edited final image from EditPictureModal
                // window.eventBus.$on('EditedImage', val => {
                //     console.log(val)
                // })
                this.sortReview()
            },
            sortReview() {
                $('#reviewPostButton').attr('disabled',true);
                const type = this.reviewType;
                this.errorData = {};
                let postUrl = '';

                let formData = new FormData();
                let file = $("#getImage").get(0).files[0];

                if(file !== undefined) {
                    $('#reviewPostButton').attr('disabled',false)
                    const extention = file.name.split(".").pop()
                    if(extention === "png" || extention === "jpg" || extention === "jpeg" || extention === "gif" ) {
                        formData.append('image', file);
                    }else if(extention === "mp4" || extention === "m4v" || extention === "MOV" || extention === "mkv") {
                        formData.append('video', file);
                    }else {
                        $('#reviewPostButton').attr('disabled',true)
                        return false
                    }
                }else if(this.formModelReview.review === '' && file === undefined ) {
                    this.errorData = {
                        message: "Your review cannot be empty",
                        isError: true,
                    };
                    return false
                }

                if (type === 'review') {
                    postUrl = 'reviews/create';
                    formData.append("match_key", this.matchId);
                }else if(type === 'update') {
                    postUrl = 'reviews/update';
                    formData.append("review_id", this.review_id);
                }

                // formData.append("fixture_id", this.matchId);
                formData.append("reviews", this.formModelReview.review);

                const config = {
                    onUploadProgress: function(progressEvent) {
                        $('.circleProgressBarClass').css('display','flex');
                        const percentCompleted = Math.round((progressEvent.loaded * 100) / progressEvent.total)
                        $('#reviewPostButton').html(`Uploading (${percentCompleted}%)`)
                    },
                }

                this.axios.post(postUrl, formData, config)
                    .then(() => {
                        // loader.hide();
                        this.errorData = {
                            message: "Your review has been uploaded successfully",
                            isError: "success",
                        };
                        this.formModelReview.review = '';
                        $("#review").val("");
                        $("#imageName").val("");
                        $("#getImage").val("");
                        window.eventBus.$emit("loadFeeds", true);
                        $("#reviewPostButton").attr("disabled", false);
                        $('.circleProgressBarClass').css('display','none');
                        $('#reviewPostButton').html(`Post`)
                        this.removeSelectedPicture()
                        $("#edit-my-poll-popup").modal("hide");
                        this.errorData = {
                            message: "",
                            isError: false,
                        };
                    })
                    .catch(error => {
                        $('.circleProgressBarClass').css('display','none');
                        $("#review").attr("disabled", false);
                        $("#reviewPostButton").attr("disabled", false);
                        $("#imageName").attr("disabled", false);
                        $("#getImage").attr("disabled", false);
                        // console.log(error)
                        this.errorData = {
                            message: error.response.status === 500 ? 'Opps! this was not meant to happen' : error.response
                                ? "Please check your Review and image (Jpg, Png - Only)"
                                : "Sorry an error occurred, check your internet",
                            isError: true,
                        };
                    });

            },

        },
        mounted() {

            // $('#myModal').on('hidden.bs.modal', function (e) {
            //     // do something...
            // })
            //
            // $('#edit-my-poll-popup').on('hide.bs.modal', function (e) {
            //     $('#quiteOption').modal('show')
            //     // alert('do you really want to quit')
            // })
            //
            // $('#edit-my-poll-popup').on('shown.bs.modal', function (e) {
            //     $('.modal-dialog').click(function () {
            //         alert('clicked backdrop')
            //     })
            // })
            //
            // $('#edit-my-poll-popup').modal({
            //     keyboard: false,
            //     backdrop: 'static',
            //     show: false,
            // });

            // set match id if emited id. this is coming from post photos button
            window.eventBus.$on("review_option", val => {
                this.matchId = val.matchId;
                this.reviewType = val.type;
                this.review_id = val.review_id;
                this.index = val.index;
                this.feedDetails = val.feed;
                this.formModelReview.review = val.feed ? val.feed.review : '';

                // set images and videos for update if present in the returned val
                // console.log(val)

                if (val.type === 'update') {
                    if (val.feed && val.feed.images.length > 0) {
                        const im = val.feed.images[0].src;
                        if (val.feed.images[0].type === '1') {
                            const imagee = `<img src="${im}">`
                            $('.uploadedFile').css('display', 'flex');
                            $('.uploadedFileCancle').css('display', 'flex');
                            $('.uploadedFile span').html(imagee)
                        } else {
                            $('.uploadedFile').css('display', 'block');
                            $('.uploadedFileCancle').css('display', 'flex');
                            this.selectedVideoFile = im
                            $('.popUpModVid').attr('src', im)
                        }
                    }else {
                        $('.uploadedFile').css('display', 'none');
                        $('.uploadedFileCancle').css('display', 'none');
                    }
                }

                this.setMatch();
            });

        },

    }
</script>

<style scoped>

</style>
